import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);

/**
 * 获取动态路由组件
 * @param componentName dirNameIndex  组件名字，目录(模块)索引
 * **/
const _loadComponent = (componentName, dirNameIndex) => {
  // 首页，预约挂号,地图导航,排队候诊,报告单
  let dirNameArr = ['Home','Registered','Map','Queue','Reportcard']
  return () => import (`@/components/pages/${dirNameArr[dirNameIndex]}/${componentName}`)
}

/**
 * 获取路由配置
 * @param pathName dirNameIndex restParams  名称，目录(模块)索引, metaParam为字符串时设置title，其它则解构参数
 * **/
const _getRouterPath = (pathName, dirNameIndex, restParams) => (Object.assign({}, {
  path: pathName,
  name: pathName,
  component: _loadComponent(pathName, dirNameIndex)
}, typeof restParams === 'string' ? { meta: { title: restParams } } : restParams))

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      title:'首页',
      redirect: 'home',
      component: () =>import ('@/components/index'),
      children:[
        _getRouterPath('home', 0, '首页'),
        _getRouterPath('selectDepartment', 1, '选择科室'),
        _getRouterPath('selectDoctor', 1, '选择医生'),
        _getRouterPath('doctorSpace', 1, '医生排班'),
        _getRouterPath('confirmOrder', 1, '确认订单'),
        _getRouterPath('payment', 1, '付款'),
        _getRouterPath('payerror', 1, '付款失败'),
        _getRouterPath('paysuccess', 1, '付款失败'),
        _getRouterPath('navigation', 2, '地图导航'),
        _getRouterPath('lineup', 3, '排队候诊'),
        _getRouterPath('reportcardType', 4, '选择报告单'),
      ]
    },
    
  ],
});





